.assistant-face {
    display: flex;
    justify-content: center;
    align-items: center;
    width: calc(100% - 20px);
    height: 100%;
    padding: 0 10px;
    gap: 10px;;
}

.assistant-face-logo {
    width: 40px;
    height: 40px;
    border-radius: 15px;
}

.assistant-face-talk {
    max-width: calc(100% - 40px);
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    overflow: auto;
    border-radius: var(--border-radius);
}

.assistant-face-talk-item {
    flex-shrink: 0;
    background: var(--primary-color);
    height: 80%;
    border-radius: var(--border-radius);
    padding: 0 10px;
    color: white;
}
.assistant-face-talk-item-close {
    color: var(--danger-color);
    cursor: pointer;
    font-weight: bold;
    font-size: var(--font-size-mid);
}